import './App.css';
import axios from 'axios'
import { useEffect, useState } from 'react'

function App() {
  const [contatos, setContatos] = useState([]);

  
  useEffect(() => {
    axios.get('https://contato-api-rps.herokuapp.com/')
	  .then(function (response) {
	    setContatos(response.data);
	  })
	  .catch(function (error) {
	    console.log(error);
	  });
    
  }, []);

  function atualizar () {
	axios.get('https://contato-api-rps.herokuapp.com/')
		  .then(function (response) {
		    setContatos(response.data);
		  })
		  .catch(function (error) {
		    console.log(error);
		  });
	}

  function deletar (id) {
	axios.delete('https://contato-api-rps.herokuapp.com/' + id)
		  .then(function (response) {
		    atualizar()
		  })
		  .catch(function (error) {
		    console.log(error);
		  });
	}

  return (
    <div className="App">
	<h2>Contatos:</h2>
	<ul class="list-group lista">{contatos.map(contato => (<li key={contato._id} class="list-group-item d-flex justify-content-between align-items-center rounded shadow-sm">{contato.nome} - {contato.numero} <button type="button" class="btn-close" aria-label="Close" onClick={() => {deletar(contato._id)}}></button></li>))}</ul>
	<button class="btn btn-primary atualizar" type="button" onClick={() => {atualizar()}} >atualizar</button>    

</div>
  );
}

export default App;
